body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
  background-color: #e0f7f4;
  color: #104d4d;
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.header {
  background: linear-gradient(135deg, #b2f4e5, #e0f7f4);
  padding: 50px 20px;
  box-shadow: 0 4px 10px rgba(93, 207, 179, 0.3);
}

.header h1 {
  margin: 0;
  font-size: 2.6rem;
  color: #064b4b;
}

.nav-links {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  gap: 30px;
}

.nav-links a {
  text-decoration: none;
  color: #064b4b;
  font-size: 1.1rem;
  font-weight: 500;
  position: relative;
  transition: color 0.3s ease;
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0%;
  height: 2px;
  background-color: #087d73;
  transition: width 0.3s ease;
}

.nav-links a:hover {
  color: #087d73;
}

.nav-links a:hover::after {
  width: 100%;
}

section {
  max-width: 1100px;
  margin: 60px auto;
  padding: 80px 40px;
  background: rgba(255, 255, 255, 0.92);
  border: 2px solid #b2f4e5;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(93, 207, 179, 0.25);
  text-align: center;
}

section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #048b7d;
  position: relative;
}

section h2::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -8px;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: #9bead7;
  border-radius: 2px;
}

section p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 10px;
}

section ul {
  list-style: none;
  padding: 0;
}

section li {
  margin: 8px 0;
  font-size: 1.1rem;
}

.app-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 700px;
  height: 80px;
  background: rgba(178, 244, 229, 0.4);
  color: #064b4b;
  font-size: 1.2rem;
  font-weight: 500;
  border: 2px solid #b2f4e5;
  border-radius: 14px;
  text-decoration: none;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
}

.app-button:hover {
  transform: translateY(-6px) scale(1.04);
  box-shadow: 0 10px 20px rgba(93, 207, 179, 0.35);
  background-color: rgba(155, 234, 215, 0.5);
}

.about-photo {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 20px;
  border: 4px solid #b2f4e5;
  box-shadow: 0 4px 10px rgba(93, 207, 179, 0.3);
  transition: transform 0.8s ease, box-shadow 0.8s ease;
}

.about-photo:hover {
  transform: rotate(360deg);
  box-shadow: 0 0 20px 4px rgba(93, 207, 179, 0.6);
}

.skills-section {
  max-width: 1100px;
  margin: 60px auto;
  padding: 80px 40px;
  background: rgba(255, 255, 255, 0.92);
  border: 2px solid #b2f4e5;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(93, 207, 179, 0.25);
}

.skills-section h2 {
  font-size: 2rem;
  margin-bottom: 25px;
  color: #048b7d;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.skill-pill {
  background-color: #b2f4e5;
  color: #064b4b;
  padding: 12px 20px;
  border-radius: 25px;
  font-size: 1.05rem;
  font-weight: 500;
  box-shadow: 0 4px 8px rgba(93, 207, 179, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
}

.skill-pill:hover {
  transform: scale(1.1);
  background-color: #9bead7;
  box-shadow: 0 6px 14px rgba(93, 207, 179, 0.4);
}

.project-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}

.footer {
  background-color: #b2f4e5;
  padding: 40px 20px;
  margin-top: 60px;
  text-align: center;
  box-shadow: 0 -4px 8px rgba(93, 207, 179, 0.2);
}

.footer p {
  margin: 5px;
  font-size: 1rem;
  color: #064b4b;
}

.social-links {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.social-links a {
  color: #064b4b;
  font-size: 1.5rem;
  transition: transform 0.3s ease, color 0.3s ease;
}

.social-links a:hover {
  color: #087d73;
  transform: scale(1.2);
}
